<template>
    <div id="left-layout">
        <ul>
            <li ref="one" v-for="(item,index) in list"  :key="index">
              <div class="title">
                <i :class="item.class"></i><span>{{item.label}}</span><i class="arrow fa fa-angle-right" :class="{'fa-angle-down':curActiveName === item.name}"></i>
              </div>
              <router-link tag="div" class="sec" v-for="(child,key) in item.children" :key="key" :to="{path:'/chart/'+child.name}">
                <span>{{child.label}}</span>
              </router-link>
            </li>

        </ul>
    </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "left",
  data() {
    return {
      curActiveName: "basic-line",
      list: [
        {
          name: "line-chart",
          label: "",
          class: "iconfont icon-line",
          children: [
            {
              name: "basic-line",
              label: ""
            },
            {
              name: "category-line",
              label: ""
            }
          ]
        },
        {
          name: "bar-chart",
          label: "",
          class: "iconfont icon-bar",
          children: [
            {
              name: "bar",
              label: ""
            },
            {
              name: "horizontal-bar",
              label: ""
            },
            {
              name: "group-bar",
              label: ""
            },
            {
              name: "stack-bar",
              label: ""
            },
            {
              name: "stack-bar-percent",
              label: ""
            }
          ]
        },
        {
          name: "pie-chart",
          class: "iconfont icon-pie",
          label: "",
          children: [
            {
              name: "pie-chart",
              label: ""
            },
            {
              name: "ring-chart",
              label: ""
            },
            {
              name: "rose-chart",
              label: ""
            }
          ]
        },
        {
          name: "scatter-chart",
          class: "iconfont icon-point",
          label: "",
          children: [
            {
              name: "scatter-chart",
              label: ""
            }
          ]
        },
        {
          name: "area-chart",
          class: "iconfont icon-area",
          label: "",
          children: [
            {
              name: "area-chart",
              label: ""
            }
          ]
        },
        {
          name: "boxplot-chart",
          class: "iconfont icon-box",
          label: "",
          children: [
            {
              name: "boxplot",
              label: ""
            }
          ]
        },
        {
          name: "cluster-chart",
          class: "iconfont icon-heatmap",
          label: "",
          children: [
            {
              name: "cluster",
              label: ""
            },
            {
              name: "complexCluster",
              label: ""
            },
            {
              name: "relevance",
              label: ""
            }
          ]
        },
        {
          name: "bubble-chart",
          class: "iconfont icon-bubble",
          label: "",
          children: [
            {
              name: "bubble",
              label: ""
            }
          ]
    },
    {
          name: "group-chart",
          class: "iconfont icon-bubble",
          label: "",
          children: [
            {
              name: "group",
              label: ""
            }
          ]
    },
    {
          name: "circos-chart",
          class: "iconfont icon-bubble",
          label: "",
          children: [
            {
              name: "circos",
              label: "circos"
            },
            {
              name: "risingSun",
              label: ""
            },
            {
              name:'circleTree',
              label:""
            }
          ]
    },
		{
          name: "other-chart",
          class: "iconfont icon-bubble",
          label: "",
          children: [
            {
              name: "manhattan",
              label: ""
            }
          ]
		},
		// {
        //   name: "compose-chart",
        //   class: "iconfont icon-legend",
        //   label: "",
        //   children: [
        //     {
        //       name: "compose-chart",
        //       label: ""
        //     }
        //   ]
        // }
      ]
    };
  },
  mounted() {
    $("#left-layout .title")
      .on("mouseover", function() {
        $("#left-layout .title").removeClass("active");
        $(this).addClass("active");
      })
      .on("mouseout", function() {
        $(this).removeClass("active");
      })
      .on("click", function() {
		if($(this).hasClass('show')){
			$(this).removeClass('show').parent().find('.sec').slideUp();
			$(this).find('.arrow').attr('class','arrow fa fa-angle-right');
		}else{
			$(this).addClass('show').parent().find('.sec').slideDown();
			$(this).find(".arrow").attr('class','arrow fa fa-angle-down');
		}
      });
  }
};
</script>



